import styled from "styled-components";

export default styled.pre`
  background: rgba(0, 0, 0, 0.05);
  padding: 20px;
  white-space: pre-wrap;
  font-family: "Courier New", Courier, monospace;
  font-size: 16px;
  margin-left: 20px;
  max-width: 660px;
  overflow: auto;
  @media (max-width: 640px) {
    margin-left: 0;
  }
`;



